.app {
  width: 100vw;
  height: 100vh;
  display: table;
  background-color: #1e1e1e;
}


.app-container {
  margin-left:auto; 
  margin-right:auto;
  width: 80%;
  margin-top: 14vh;
}

@media only screen and (max-width: 768px) {
  .app-container {
    font-size: .7em;
    margin-top: 10px;
  }
  
  .stat-container {
    width:60%
  }
  .text {
    font-size: 9pt;
  }
  .value {
    font-size: 20pt;
  }
}

@media only screen and (min-width: 992px) {
  .app-container {
    width: 40%;
  }
}

.input {
  opacity: 0;
  position: absolute;
}

.row {
  display: flex;
  margin: 0px 10px 0px 10px;
}

.column {
  flex: 100%;
  width: 50%;
  margin: 5px;
  display: grid;
  justify-content: center;
  align-items: center;
}
